___CSS_LOADER_EXPORT___.push([module.id, `body {
  font-family: Arial, sans-serif;
  background-color: #f0f0f0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
}

.card {
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  max-width: 320px;
  overflow: hidden;
  text-align: center;
  margin: 20px;
}

.card-image img {
  width: 100%;
  height: auto;
}

.card-content {
  padding: 16px;
}

.card-title {
  font-size: 1.25rem;
  margin-bottom: 8px;
}

.card-rating {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 8px;
}

.stars {
  color: #ffbf00;
  margin-right: 8px;
}

.rating {
  font-size: 0.875rem;
  color: #757575;
}

.card-secondary {
  color: #757575;
  font-size: 0.875rem;
  margin-bottom: 16px;
}

.card-description {
  color: #333;
  font-size: 0.875rem;
  margin-bottom: 16px;
}

.card-subtitle {
  font-size: 1rem;
  margin-bottom: 8px;
}

.card-items {
  display: flex;
  justify-content: center;
  gap: 8px;
  margin-bottom: 16px;
}

.card-item {
  background-color: #e0e0e0;
  border-radius: 12px;
  padding: 4px 12px;
  font-size: 0.875rem;
}

.card-action {
  color: #6200ee;
  text-decoration: none;
  font-size: 1rem;
  font-weight: bold;
}
